body {
  padding: 8px;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
  border: 0;
  outline: none;
  font-family: inherit;
}

p, ul {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

a {
  color: inherit;
  font-family: inherit;
  text-decoration: none;
}

.header {
  background-color: #93c4f3;
  padding-top: 16px;
  padding-bottom: 16px;
  box-shadow: 0 5px 5px rgba(150, 150, 150, .5);
}

.header-container {
  max-width: 1200px;
  flex-wrap: wrap;
  align-items: center;
  gap: 24px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
  display: flex;
}

.nav {
  flex-wrap: wrap;
  gap: 12px 24px;
  display: flex;
}

.menu-link {
  text-transform: uppercase;
  color: #ececec;
  font-weight: 600;
  position: relative;
}

.menu-link.active-link:after {
  content: "";
  width: 100%;
  height: 3px;
  background-color: #060697;
  position: absolute;
  bottom: -5px;
  left: 0;
}

.menu-link.active-link {
  color: #060697;
}

.search-form {
  flex-wrap: wrap;
  gap: 12px;
  font-size: 22px;
  display: flex;
}

.search-form input {
  border: none;
  outline: none;
  padding: 6px;
  display: inline-block;
}

.gallery {
  padding-top: 24px;
  padding-bottom: 64px;
}

.photo-card {
  box-shadow: 0 0 8px 2px rgba(150, 150, 150, .3);
}

.photo-card img {
  height: 270px;
  object-fit: cover;
  object-position: center;
}

.info {
  justify-content: space-around;
  padding: 12px 8px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.loader-btn {
  border-radius: 5px;
  align-items: center;
  gap: 6px;
  padding: 6px;
  font-size: 22px;
  display: flex;
  box-shadow: 4px 4px 4px rgba(150, 150, 150, .3);
}

.load-more-btn {
  margin-bottom: 48px;
  margin-left: auto;
  margin-right: auto;
}

.search-loader, .load-more-loader {
  width: 40px;
  height: 40px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.loader {
  width: 40px;
  height: 40px;
  box-sizing: border-box;
  border: 5px dotted #7aa9d4;
  border-radius: 50%;
  animation: rotation 2s linear infinite;
  display: inline-block;
  position: relative;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.go-top {
  cursor: pointer;
  width: 50px;
  height: 50px;
  z-index: 2;
  background-color: rgba(122, 169, 212, .9);
  border: 2px solid rgba(230, 230, 230, .5);
  border-radius: 25px;
  font-size: 24px;
  transition: background-color .25s ease-in-out, border .25s ease-in-out;
  display: none;
  position: fixed;
  bottom: 56px;
  right: 24px;
}

.go-top div {
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.go-top div svg {
  fill: #fff;
}

.go-top:hover {
  background-color: rgba(122, 169, 212, .7);
  border: 2px solid rgba(230, 230, 230, .8);
}

.go-top--show {
  display: block;
}

.is-hidden {
  display: none;
}

